.trial {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 15px;
  padding-top: 50px;
  overflow: hidden;

  .trial__left-side {

    padding-bottom: 40px;
     img {
       width: 200px;
       height: 220px;
     }
  }

  .trial__right-side {
    .trial__title {
      font-style: normal;
      font-weight: bold;
      font-size: 40px;
      line-height: 40px;
      letter-spacing: -0.357143px;
      color: #2B292D;
      padding-bottom: 18px;
    }

    .trial__subtitle {
      font-style: normal;
      font-weight: normal;
      font-size: 24px;
      line-height: 34px;
      color: #2B292D;
      padding-bottom: 40px;
    }

    .trial__email {
      background: #FFFFFF;
      border: 1px solid rgba(43, 41, 45, 0.2);
      box-sizing: border-box;
      border-radius: 2px;
      width: 100%;
      height: 48px;
      margin-bottom: 12px;
    }

    .trial__email:focus {
      outline: 1px solid #3040C4;
    }

    .trial__email[placeholder] {
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 13px;
      text-align: center;
      color: #2B292D;
    }

    .trial__text {
      margin-top: 10px;
      margin-bottom: 50px;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 22px;
      color: rgba(43, 41, 45, 0.6);
    }

    .trial__link {
      text-decoration: none;
      color: #3142C6;
      transition: .2s ease;
    }
    .trial__link:hover {
      color: #2B292D;
      transition: .2s ease;
    }
  }
}

@media (min-width: 768px) {
  .trial {
    flex-direction: row;
    justify-content: space-evenly;
    padding-bottom: 72px;

    .trial__left-side {
      padding: 0;
      img {
        width: 270px;
        height: 296px;
      }
    }

    .trial__right-side {
      max-width: 560px;

      .trial__subtitle {
        width: 560px;
      }
      .trial__form {
        form{
          display: flex;

          .trial__email {
            width: 300px;
            height: 48px;
          }

          .trial__btn {
            margin-left: 10px;
          }

          .trial__email[placeholder] {
            font-style: normal;
            font-weight: normal;
            text-align: left;
            font-size: 18px;
            line-height: 13px;
            color: #2B292D;
            padding-left: 10px;
          }
        }
      }
    }
  }
}

@media (min-width: 992px) {
  .trial {
    justify-content: center;
    .trial__right-side .trial__form form .trial__email{
      width: 385px;
    }
    .trial__left-side {
      margin-right: 120px;
    }
  }
}
